.App {
  text-align: center;
  padding: 0;
  border: 0;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"], input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}



.suggestions {
  width: 250px;
  align-self: center;
  margin: 0;
  font-size: 14px;
  margin-left: 14px;
  margin-top: 50px;
  position: absolute;
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}


.suggestions > li:hover {
  background-color: #BCDCDB;
}



.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going, .visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.number-of-events {
  width: 80px;
  margin: 20px 0 50px;
  text-align: right;
}

.top-header {
  background-color: #fff;
  height: 70px;
  border: 0;
}
.meetup-logo {
  margin-left: 14px;
  margin-top: 15px;

  width: 125px;
  height: 44px;
  float: left;
  background-image: url(../src/img/meetup-logo.svg);
  background-size: cover;
}

.next-header {
  /* background-color: #00465d; */
  /* background-color: fuchsia; */
  background-color: green;
  margin: 0;
  height: 170px;
  color: #fff;
  padding-top: 2%;
  font-size: 34px;
}
.how-many-events {
  color: #BBCCD2;
  margin-top: -90px;
  height: 120px;
  font-size: 16px;
  font-weight: 600;
}
.search-area-wrap {
  width: 100%;
  position: absolute;
  display: block;
}
.search-area {
  background-color: #000;
  width: 960px;
  margin: -58px auto;
  height: 70px;
  display: block;
}
.city-search{
  width: 250px;
  float: left;
  margin-left: 14px;
  margin-top: 8px;
  height: 43px !important;
  border-radius: 1 !important;
}
.Alert {
  text-align: center;
  background-color: #eee;
  cursor: default;
  border-radius: 5px;
}
.offline-alert-div {
  width: 40%;
  margin-left: 30%;
  margin-top: 30px;
}

.event-search {
  width: 250px;
  float: left;
  margin-left: 14px;
  margin-top: 8px;
  height: 43px !important;
  border-radius: 1 !important;
}
.event-searchbar {
  width: 250px;
  float: left;
  margin-left: 14px;
  margin-top: 12px;
  height: 43px !important;
  border-radius: 1 !important;
}
.event-searchbar::placeholder {
  font-style: italic;
}
.chart-button {
  color: rgb(179, 179, 179);
  width: 150px;
  height: 50px;
  margin-top: 10px;
  float: right;
  margin-right: 10px;
  background-color: #000;
  border-radius: 3px;
  padding-top: 14px;
  display: inline-block;
  font-weight: 600;
  border: solid 1px;
}
.chart-button:hover {
  background-color: #4A4E55;
  color: #fff;
  cursor: pointer;
}
.next-to-search {
  color: #fff;
  margin-top: 25px;
  margin-bottom: 28px;
  float: left;
  margin-left: 15px;
  font-weight: 600;
}
.miles {
  display: inline;
  border-bottom: 1px solid #888;
  padding-bottom: 3px;
}
.miles:hover {
  border-bottom: 1px solid #fff;
  cursor: pointer;
}
.city {
    display: inline;
    border-bottom: 1px solid #888;
    padding-bottom: 3px;
}
.city:hover {
  border-bottom: 1px solid #fff;
  cursor: pointer;
}
.city-searchbar {
  width: 250px;
  float: left;
  margin-left: 14px;
  margin-top: 8px;
  height: 43px !important;
  border-radius: 1 !important;
  z-index: 2;
  position: relative;
}
.city-searchbar::placeholder {
  font-style: italic;
}
.city-searchbar:focus {
border-color: royalblue;
}
.the-triangle {
  width: 20px;
  height: 20px;
  background-color: #fff;
  transform: rotate(45deg);
  margin-left: 60px;
  margin-top: -25px;
  display: absolute;
  z-index: 10;
}
.popup-container {
  display: none;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  left: 0;
  margin-top: -270px;
  position: absolute;
}
.miles-popup {
  background-color: #fff;
  z-index: 2;
  width: 140px;
  height: 270px;
  padding-top: 16px;
  border-radius: 5px;
  margin-left: 36%;
  margin-top: 277px;
  position: absolute;
  box-shadow: 0 4px 80px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
.mile-list-item {
  font-size: 18px;
  width: 107px;
  height: 30px;
  padding: 8px;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: left;
  margin-left: 15px;
  color: rgb(129, 129, 129);
  border-radius: 2px;
}
.mile-list-item:hover {
  background-color: #F63F60;
  color: #fff;
}
.search-popup-container {
  display: none;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  left: 0;
  margin-top: -270px;
  position: absolute;
}
.search-the-triangle {
  width: 20px;
  height: 20px;
  background-color:#fff;
  transform: rotate(45deg);
  margin-left: 48%;
  margin-top: 265px;
  display: absolute;
  z-index: 1;
}
.search-popup {
  background-color: #fff;
  width: 280px;
  height: 60px;
  border-radius: 3px;
  box-shadow: 0 4px 80px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-left: 39%;
  margin-top: -15px;
  z-index: 4;
}
.chart-modal-container {
  display: none;
  background-color: rgba(0, 0, 0, 0.4);
  height: 100vh;
  width: 100vw;
  position: fixed;
  margin-top: -280px;
  z-index: 1;
}
.chart-modal{
  background-color: #fff;
  width: 80%;
  height: 95%;
  margin-top: 6%;
  margin-left: 10%;
  border-radius: 5px;
  position: absolute;
  z-index: 1;
}
.responsive-container {
  position: relative;
  height: 700px !important;
}
.scatter-wrapper {
  position: relative;
  height: 80%;
  overflow-y: scroll;
}
.modal-title {
  padding-top: 2%;
}
.close-modal-x {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
  font-weight: bold;
  cursor: pointer;
  height: 40px;
  width: 40px;
  padding-top: 10px;
  border-radius: 50%;
}
.close-modal-x:hover {
  background-color: rgb(209, 209, 209);
  transition-duration: 300ms;
  color: rgb(97, 97, 97);

}
.the-chart {
  margin-top: 90px;
}


/* ---------------------------- EVENTS ---------------------------------- */

