  height: 175px;
  display: inline-flexbox;
  margin-top: 16px;
  background-color: #185f77;
  border-radius: 3px;
  cursor: pointer;
}
.event-holder:hover {
opacity: 0.8;
}
.event {
background-size: cover;
color: #fff;
width: 309.33px;
height: 175px
}
.name-area {
  padding-top: 58%;
  height: 87.5px;
  width: 309.33px;
  position: relative;

}
.event-name {
  color: #fff;
  text-align: left;
  bottom: 0;
  position: absolute;
  padding-left: 10px;
