.no-event-warning {
  margin-top: 100px;
}

/* ------------------------THE MODAL----------------------------- */

.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  
}
.modal-content {
  background-color: #fefefe;
  margin: 1% auto;
  height: 95vh;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  border-radius: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.the-x {
  float: right;
  font-weight: bold;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  padding-top: 10px;
  margin-top: 0;
}
.the-x:hover {
  cursor: pointer;
  background-color: rgb(230, 230, 230);
  transition-duration: 300ms;
  color: rgb(75, 75, 75);
}
.modal-top-half {
  width: 100%;
  height: 50%;
  display: block;
}
.modal-img {
  background-size: cover;
  width: 45%;
  height: 300px;
  border-radius: 5px;
  margin: 15px;
  margin-bottom: 0;
  display: inline-block;
}
.modal-event-info {
  float: right;
  margin-top: 10px;
  margin-right: 100px;
  width: 400px;
  text-align: left;
}
.modal-event-name {
  font-weight: bold;
}
.modal-event-location {
  font-size: 14px;
  color: #000;
  text-align: left;
  display: block;
  margin-bottom: 5px;
}
.modal-event-date {
  font-size: 14px;
  color: #000;
  display: block;
  width: 100%;
  float: left;
  margin-top: 5px;
  margin-bottom: 5px;
}
.modal-event-link {
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: block;
  color: #185f77;
  float: left;
  margin-top: 5px;
  margin-bottom: 5px;
}
.modal-event-link:hover {
  text-decoration: underline;
}
.modal-bottom-half {
  width: 100%;
  height: 50%;
  display: block;
}
.the-details {
  padding-top: 20px;
}
.modal-event-description {
  float: left;
  font-size: 14px;
  margin-top: -140px;
  width: 45%;
  margin-left: 55px;
  text-align: left;
}
.modal-the-description {
  overflow: scroll;
  height: 275px;
}
.pie-container {
 margin-left: 300px;
 padding-bottom: 20%;
}

/* -------------------Media Styles-------------------- */
@media (max-width: 1600px) {
  .miles-popup {
    margin-left: 38%;
  }
}
@media (max-width: 1440px) {
  .miles-popup {
    margin-left: 37%;
  }
}
@media (max-width: 1350px) {
  .chart-button {
    display: none;
  }
  .chart-modal-container {
    display: none;
    opacity: 0;
  }
  .chart-modal {
    display: none;
  }
  .the-triangle {
    margin-left: 60px;
  }
  .miles-popup {
    margin-left: 36%;
  }

  .modal-img {
    width: 100%;
    margin: 0;
  }
  .modal-event-info {
    float: left;
    margin-left: 20px;
  }
  .modal-event-description {
    margin-top: 20px;
    width: 80%;
    margin-left: 20px;
  }
  .modal-event-description > h3 {
    margin-bottom: 5px;
  }
   .recharts-wrapper {
     display: none;
 }
}
@media (max-width: 1260px) {
  .miles-popup {
    margin-left: 34%;
  }
}
@media (max-width: 1090px) {
  .miles-popup {
    margin-left: 32%;
  }
}
@media (max-width: 975px) {
  .miles-popup {
    margin-left: 295px;
  }
}
@media (max-width: 950px) {
  .search-popup {
    margin-left: 370px;
  }
  .search-the-triangle {
    margin-left: 457px;
  }
}
